@import "../../style/variables.scss";

.teamSection01 {
  position: relative;
  width: 100%;
  height: 100vh;
  background-color: #121417;

  &-wrap {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &-text {
    max-width: calc(960px + 62px);
    padding: 150px 31px;
    margin: 0 auto;
    z-index: 10;
    transition: all 0.5s;
    opacity: 0;
    transform: translateY(10%);
    &-title {
      font-size: 3.8rem;
      font-weight: bold;
      line-height: 52px;
      color: #fff;
      margin-bottom: 25px;
      .brMobile {
        display: none;
      }
    }
    &-list {
      > li {
        display: inline-flex;
        flex-direction: column;
        width: 400px;
        margin-top: 51px;
        p:nth-of-type(1) {
          font-size: 2.1rem;
          line-height: 1.25;
          color: #b0b8c1;
          margin-bottom: 9px;
        }
        p:nth-of-type(2) {
          font-size: 6.4rem;
          font-weight: bold;
          line-height: 1.25;
          color: #fff;
        }
      }
    }
    &-date {
      font-size: 1.4rem;
      color: #6b7684;
      margin-top: 36.26px;
    }
    &.on {
      transform: translateY(0px);
      // transform: translate(-50%, -50%);
      opacity: 1;
    }
  }

  &-graph {
    position: absolute;
    bottom: -10%;
    left: 0;
    max-width: 100%;
    height: 100%;
    z-index: 1;
  }

  @include mobile {
    .teamSection01 {
      &-wrap {
      }

      &-text {
        width: 100%;
        &-title {
          text-align: center;
          font-size: 2.8rem;
          line-height: 39.2px;
          text-align: center;
          margin-bottom: 40px;

          .brPc {
            display: none;
          }
          .brMobile {
            display: inline-block;
          }
        }
        &-list {
          width: 100%;
          > li {
            display: flex;
            flex-direction: column;
            p:nth-of-type(1) {
              font-size: 1.7rem;
            }
            p:nth-of-type(2) {
              font-size: 4rem;
            }
          }
        }
        &-date {
        }
      }

      &-graph {
      }
    }
  }
}
