@import "../style/variables";

.hoverList {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  @include mobile {
    padding: 0 15px;
  }

  &-item {
    position: relative;
    flex-shrink: 0;
    width: 466px;
    height: 372.8px;
    border-radius: 12px;
    margin: 14px 0;
    overflow: hidden;
    @include mobile {
      width: 100%;
    }

    &-bg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: no-repeat center / cover;
      z-index: -1;
      &.hover {
        filter: blur(10px);
        &::before {
          position: absolute;
          inset: 0px;
          content: "";
          background: rgba(159, 198, 242, 0.2);
        }
      }
    }

    &-txt {
      padding: 32px;
      @include mobile {
        padding: 24px;
      }

      &-hidden {
        opacity: 0;
        height: 0;
        font-size: 1.5rem;
        margin-bottom: 8px;
        color: rgb(107, 118, 132);
        letter-spacing: 0.01em;
        transition: opacity 0.3s ease 0s, height 0.3s ease 0s;
        &.hover {
          height: 15px;
          opacity: 1;
        }
        @include mobile {
          &.hover {
            font-size: 1rem;
            margin-bottom: 2px;
          }
        }
      }

      &-tit {
        font-size: 3.2rem;
        font-weight: 700;
        line-height: 1.4;
        color: rgb(51, 61, 75);
        white-space: pre;
        margin-bottom: 20px;
        transition: all 0.3s ease 0s;
        &.hover {
          font-size: 2.6rem;
          font-weight: 700;
        }
        @include mobile {
          font-size: 2.1rem;
          &.hover {
            font-size: 2.1rem;
            margin-bottom: 10px;
          }
        }
      }

      &-icon {
        transition: all 0.3s ease;
        height: 24px;
        opacity: 1;
        img {
          width: 24px;
          height: 24px;
        }
        &.hover {
          opacity: 0;
          height: 0;
        }
      }

      &-des {
        display: none;
        font-size: 1.4rem;
        font-weight: 500;
        line-height: 21px;
        color: rgb(51, 61, 75);
        transition: all 0.5s;
        white-space: pre-line;
        &.hover {
          display: block;
        }
        @include mobile {
          font-size: 1.3rem;
        }
      }
    }
  }
}
