@import "../../style/variables";

.tossCertSection02 {
  max-width: calc(1044px + 96px);
  padding: 0 48px;
  margin: 0 auto;
  @include mobile {
    padding: 50px 24px 0;
  }

  &-list {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    @include mobile {
      flex-direction: column;
      align-items: flex-start;
      height: auto;
      padding: 30px 0px;
    }

    &-txt {
      flex-basis: 50%;
      p:nth-of-type(1) {
        font-size: 5.6rem;
        font-weight: 700;
        line-height: 1.3;
        color: rgba(2, 9, 19, 0.91);
        margin-bottom: 28px;
        word-break: keep-all;
      }
      p:nth-of-type(2) {
        font-size: 2rem;
        font-weight: 700;
        line-height: 1.6;
        color: rgba(3, 18, 40, 0.7);
      }
      @include tablet {
        p:nth-of-type(1) {
          font-size: 3.6rem;
        }
        p:nth-of-type(2) {
          font-size: 2rem;
        }
      }
      @include mobile {
        display: flex;
        flex-direction: column;
        justify-content: center;
        flex-basis: auto;
        p:nth-of-type(1) {
          font-size: 3.2rem;
        }
        p:nth-of-type(2) {
          max-width: 327px;
          word-break: keep-all;
          font-size: 1.7rem;
          font-weight: 400;
          letter-spacing: 0;
          word-break: keep-all;
          br {
            display: none;
          }
        }
      }
    }

    &-img {
      flex-basis: 50%;
      > img {
        width: 100%;
        object-fit: contain;
      }
      @include mobile {
        display: flex;
        justify-content: center;
        width: 100%;
        max-height: 50%;
        flex-basis: auto;
        > img {
          width: 100%;
          height: 100%;
        }
      }
    }
  }
}
