@import "../../style/variables";

.tossCertSection04 {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 120px 24px;

  p:nth-of-type(1) {
    max-width: 485px;
    font-size: 5.6rem;
    font-weight: 700;
    line-height: 1.3;
    color: rgba(2, 9, 19, 0.91);
    margin-bottom: 20px;
    @include tablet {
      font-size: 3.6rem;
    }
    @include mobile {
      font-size: 2.6rem;
    }
  }

  p:nth-of-type(2) {
    max-width: 400px;
    font-size: 2rem;
    line-height: 1.6;
    text-align: center;
    color: rgba(3, 18, 40, 0.7);
    word-break: keep-all;
    @include tablet {
      font-size: 2rem;
    }
    @include mobile {
      width: 100%;
      font-size: 1.7rem;
      br {
        display: none;
        word-break: keep-all;
      }
    }
  }
  button {
    padding: 18px 28px;
    margin-top: 30px;
    cursor: pointer;
    background-color: #3182f6;
    font-size: 1.7rem;
    color: #fff;
    line-height: 1.2em;
    border-radius: 8px;
    &:hover {
      background-color: $hover;
    }
  }
}
