@import "../../style//variables";

.teamSection04 {
  &-wrap {
    max-width: 960px;
    padding: 175px 0;
    margin: 0 auto;
    @media (max-width: 930px) {
      max-width: 100%;
      padding: 79px 14px;
    }

    &-title {
      p:nth-of-type(1) {
        font-size: 5.2rem;
        font-weight: 700;
        margin-bottom: 13px;
        text-align: center;
      }
      p:nth-of-type(2) {
        font-size: 2.4rem;
        margin-bottom: 42px;
        text-align: center;
        color: #4e5968;
        .brMobile {
          display: none;
        }
      }
      @media (max-width: 930px) {
        p:nth-of-type(1) {
          font-size: 3.6rem;
          margin-bottom: 18px;
        }
        p:nth-of-type(2) {
          font-size: 2rem;
          line-height: 1.45em;
          .brMobile {
            display: inline-block;
          }
        }
      }
    }

    &-list {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;

      &-item {
        position: relative;
        width: 458px;
        height: 523px;
        border-radius: 16px;
        padding: 32px;
        margin: 14px 0;
        overflow: hidden;
        @media (max-width: 930px) {
          width: 100%;
        }

        &-bg {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          border-radius: 16px;
          z-index: -1;
          &.on {
            filter: blur(30px);
            &::before {
              position: absolute;
              inset: 0px;
              content: "";
              background: rgba(255, 255, 255, 0.6);
            }
          }
          @include mobile {
            // height: 392px;
          }
        }

        // plus 버튼 누르기 전
        &-cate {
          position: relative;
          display: block;
          opacity: 1;
          font-size: 1.5rem;
          margin-bottom: 8px;
          transition: all 0.3s ease;
          height: 24px;
          z-index: 1;
          &.on {
            opacity: 0;
            height: 0;
          }
        }

        &-title {
          position: relative;
          margin-bottom: 20px;
          font-size: 3.2rem;
          font-weight: 700;
          color: #333d4b;
          line-height: 41px;
          white-space: pre-wrap;
          z-index: 2;
          transition: all 0.3s ease;
          &.on {
            font-size: 2.5rem;
            line-height: 33px;
          }
          @media (max-width: 930px) {
            font-size: 2.7rem;
            line-height: 1.35em;
            white-space: normal;
            word-break: keep-all;
          }
        }

        &-plus {
          display: block;
          // opacity: 1;
          height: 24px;
          transition: all 0.3s ease;
          img {
            width: 100%;
            height: 100%;
          }
          &.on {
            width: 0;
            height: 0;
          }
        }

        // plus 버튼 누른 후
        &-content {
          opacity: 0;
          font-size: 1.7rem;
          line-height: 1.5;
          transition: all 0.3s;
          white-space: pre-wrap;
          transition: all 0.3s ease;
          &.on {
            opacity: 1;
          }
          @include mobile {
            white-space: normal;
          }
        }
        &-question {
          opacity: 0;
          font-size: 1.6rem;
          color: rgb(49, 130, 246);
          line-height: 20px;
          margin: 24px 0 16px 0;
          &.on {
            opacity: 1;
          }
        }
        &-close {
          opacity: 0;
          transition: all 0.3s ease;
          img {
            width: 24px;
            height: 24px;
          }
          &.on {
            position: absolute;
            bottom: 32px;
            opacity: 1;
          }
        }
      }
    }
  }

  // @media (max-width: 930px) {
